import React, { useState } from "react";

function Report() {
  const [formData, setFormData] = useState({
    productId: "",
    reason: "",
    details: "",
  });

  const [submitted, setSubmitted] = useState(false);

  //   
  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData({
      ...formData,
      [name]: value,
    });
  };

  //   
  const handleSubmit = (e) => {
    e.preventDefault();
    console.log("  :", formData);
    // TODO:    API   
    setSubmitted(true); //    
  };

  return (
    <div style={{ padding: "20px", fontFamily: "Arial, sans-serif" }}>
      <h1 style={{ textAlign: "center" }}> </h1>
      {submitted ? (
        <div style={{ textAlign: "center", marginTop: "20px" }}>
          <h2>  .</h2>
        </div>
      ) : (
        <form
          onSubmit={handleSubmit}
          style={{
            maxWidth: "600px",
            margin: "0 auto",
            backgroundColor: "#f9f9f9",
            padding: "20px",
            borderRadius: "10px",
            boxShadow: "0 4px 8px rgba(0, 0, 0, 0.1)",
          }}
        >
          <div style={{ marginBottom: "15px" }}>
            <label
              htmlFor="productId"
              style={{ display: "block", marginBottom: "5px", fontWeight: "bold" }}
            >
               ID:
            </label>
            <input
              type="text"
              id="productId"
              name="productId"
              value={formData.productId}
              onChange={handleChange}
              placeholder="  ID "
              required
              style={{
                width: "100%",
                padding: "10px",
                border: "1px solid #ccc",
                borderRadius: "5px",
              }}
            />
          </div>
          <div style={{ marginBottom: "15px" }}>
            <label
              htmlFor="reason"
              style={{ display: "block", marginBottom: "5px", fontWeight: "bold" }}
            >
               :
            </label>
            <select
              id="reason"
              name="reason"
              value={formData.reason}
              onChange={handleChange}
              required
              style={{
                width: "100%",
                padding: "10px",
                border: "1px solid #ccc",
                borderRadius: "5px",
              }}
            >
              <option value="">  </option>
              <option value=" "> </option>
              <option value=" "> </option>
              <option value=" "> </option>
              <option value=""></option>
            </select>
          </div>
          <div style={{ marginBottom: "15px" }}>
            <label
              htmlFor="details"
              style={{ display: "block", marginBottom: "5px", fontWeight: "bold" }}
            >
               :
            </label>
            <textarea
              id="details"
              name="details"
              value={formData.details}
              onChange={handleChange}
              placeholder="   "
              required
              rows="5"
              style={{
                width: "100%",
                padding: "10px",
                border: "1px solid #ccc",
                borderRadius: "5px",
                resize: "none",
              }}
            ></textarea>
          </div>
          <button
            type="submit"
            style={{
              width: "100%",
              padding: "10px",
              backgroundColor: "#007BFF",
              color: "white",
              border: "none",
              borderRadius: "5px",
              cursor: "pointer",
              fontWeight: "bold",
            }}
          >
             
          </button>
        </form>
      )}
    </div>
  );
}

export default Report;
