import React, { useState } from "react";

function DeliveryManage() {
  const [addresses, setAddresses] = useState([
    { id: 1, name: "", address: "   123" },
    { id: 2, name: "", address: "   456" },
    { id: 2, name: "", address: "   456" },
  ]);

  const [formData, setFormData] = useState({
    name: "",
    address: "",
  });

  //   
  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData({
      ...formData,
      [name]: value,
    });
  };

  //   
  const handleAddAddress = (e) => {
    e.preventDefault();
    if (!formData.name || !formData.address) {
      alert("  !");
      return;
    }
    const newAddress = {
      id: addresses.length + 1,
      name: formData.name,
      address: formData.address,
    };
    setAddresses([...addresses, newAddress]);
    setFormData({ name: "", address: "" });
  };

  //   
  const handleDeleteAddress = (id) => {
    const updatedAddresses = addresses.filter((item) => item.id !== id);
    setAddresses(updatedAddresses);
  };

  return (
    <div style={{ padding: "20px", fontFamily: "Arial, sans-serif" }}>
      <h1 style={{ textAlign: "center" }}> </h1>

      {/*   */}
      <table style={{ width: "100%", borderCollapse: "collapse", marginTop: "20px" }}>
        <thead>
          <tr style={{ borderBottom: "2px solid #ccc" }}>
            <th style={{ padding: "10px", textAlign: "center" }}></th>
            <th style={{ padding: "10px", textAlign: "left" }}></th>
            <th style={{ padding: "10px", textAlign: "left" }}></th>
            <th style={{ padding: "10px", textAlign: "center" }}></th>
          </tr>
        </thead>
        <tbody>
          {addresses.map((item) => (
            <tr key={item.id} style={{ borderBottom: "1px solid #ddd" }}>
              <td style={{ padding: "10px", textAlign: "center" }}>{item.id}</td>
              <td style={{ padding: "10px" }}>{item.name}</td>
              <td style={{ padding: "10px" }}>{item.address}</td>
              <td style={{ padding: "10px", textAlign: "center" }}>
                <button
                  onClick={() => handleDeleteAddress(item.id)}
                  style={{
                    padding: "5px 10px",
                    backgroundColor: "#FF6347",
                    color: "white",
                    border: "none",
                    borderRadius: "5px",
                    cursor: "pointer",
                  }}
                >
                  
                </button>
              </td>
            </tr>
          ))}
        </tbody>
      </table>

      {/*    */}
      <form onSubmit={handleAddAddress} style={{ marginTop: "20px", maxWidth: "600px", margin: "0 auto" }}>
        <div style={{ marginBottom: "10px" }}>
          <label style={{ display: "block", marginBottom: "5px" }}>:</label>
          <input
            type="text"
            name="name"
            value={formData.name}
            onChange={handleChange}
            required
            style={{ width: "100%", padding: "8px", border: "1px solid #ccc", borderRadius: "4px" }}
          />
        </div>
        <div style={{ marginBottom: "10px" }}>
          <label style={{ display: "block", marginBottom: "5px" }}>:</label>
          <input
            type="text"
            name="address"
            value={formData.address}
            onChange={handleChange}
            required
            style={{ width: "100%", padding: "8px", border: "1px solid #ccc", borderRadius: "4px" }}
          />
        </div>
        <div style={{ textAlign: "right" }}>
          <button
            type="submit"
            style={{
              padding: "10px 20px",
              backgroundColor: "#007BFF",
              color: "white",
              border: "none",
              borderRadius: "5px",
              cursor: "pointer",
            }}
          >
             
          </button>
        </div>
      </form>
    </div>
  );
}

export default DeliveryManage;
